.header {
  margin-bottom: 1em;
  padding: 1em;
  padding-left: 2em;
  padding-right: 2em;
}

.header .head h1,
.header .head h2 {
  color: #FFFFFF;
  font-weight: 400;
  margin: 0;
}

.header .head h1 {
  font-size: 6em;
}

.header .head h2 {
  font-size: 3em;
  min-width: 350px;
}

.header .social {
  color: #FFFFFF;
  margin: 0px;
}

.header .social a {
  display: inline;
  margin: 10px;
  opacity: 0.9;
  transition: 0.3s;
}

.header .social a:hover {
  opacity: 1;
  transition: 0.3s;
}

.tile {
  background-color: #FFFFFF;
  padding: 1em;
  border-radius: 5px;
  margin-top: 1em;
  -webkit-box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.3);
  -moz-box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.3);
  box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.3);
}

.tile p {
  margin: 0;
}

.well {
  margin: 20px;
}

.chip {
  cursor: pointer;
  flex-basis: 210px;
  margin: 10px;
  padding: 12px;
  background-color: #FFFFFF;
  border-radius: 5px;
  -webkit-box-shadow: 0px 0px 12px 0px rgba(0, 0, 0, 0.3);
  -moz-box-shadow: 0px 0px 12px 0px rgba(0, 0, 0, 0.3);
  box-shadow: 0px 0px 12px 0px rgba(0, 0, 0, 0.3);
  transition: 0.2s;
}

.chip span {
  color: #333333;
  font-size: 1em;
  margin-left: 5px;
}

.chip:hover {
  transition: 0.2s;
  -webkit-box-shadow: 0px 0px 12px 3px rgba(0, 0, 0, 0.3);
  -moz-box-shadow: 0px 0px 12px 3px rgba(0, 0, 0, 0.3);
  box-shadow: 0px 0px 12px 3px rgba(0, 0, 0, 0.3);
}

.alienbox {
  background-color: #FFFFFF;
  margin: 0 auto;
  padding: 2em;
  padding-bottom: 4em;
}

.alienbox h1 {
  font-weight: 400;
  text-align: center;
  font-size: 4em;
  margin: 0;
  color: #FF6600;
}

.alienbox .content {
  margin-top: 1em;
}

.alienbox p {
  font-size: 1.2em;
  margin: .5em;
}

.alienbox .screenshot {
  width: 90%;
}

.alienbox .go {
  background-color: #FF6600;
  padding: 0.5em;
  text-align: center;
  color: #FFFFFF;
  font-size: 1.5em;
  border-radius: 5px;
  -webkit-box-shadow: 0px 0px 12px 0px rgba(255, 84, 0, 0.5);
  -moz-box-shadow: 0px 0px 12px 0px rgba(255, 84, 0, 0.5);
  box-shadow: 0px 0px 12px 0px rgba(255, 84, 0, 0.5);
  transition: 0.3s;
}

.alienbox .go:hover {
  transition: 0.3s;
  background-color: #FF8330;
  -webkit-box-shadow: 0px 0px 12px 2px rgba(255, 84, 0, 0.5);
  -moz-box-shadow: 0px 0px 12px 2px rgba(255, 84, 0, 0.5);
  box-shadow: 0px 0px 12px 2px rgba(255, 84, 0, 0.5);
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

