// chips styles
.well {
	margin: 20px;
}
.chip {
	cursor: pointer;
	flex-basis: 210px;
	margin: 10px;
	padding: 12px;
	background-color: #FFFFFF;
	border-radius: 5px;
	@include box-shadow(0px, 0px, 12px, 0px, rgba(0, 0, 0, 0.3));
	span {
		color: #333333;
		font-size: 1em;
		margin-left: 5px;
	}
	transition: 0.2s;
}
.chip:hover {
	transition: 0.2s;
	@include box-shadow(0px, 0px, 12px, 3px, rgba(0, 0, 0, 0.3));
}
