// Normalize Styles
//@import 'node_modules/normalize.css/normalize';
@import "./font-awesome-4.5.0/scss/font-awesome";
@import "node_modules/flexboxgrid/css/flexboxgrid";
@import url(https://fonts.googleapis.com/css?family=Roboto:400,500,700);

// Import Modules
@import '../_modules/organisms/header/header';
@import '../_modules/organisms/tile/tile';
@import '../_modules/organisms/chips/chips';
@import '../_modules/organisms/alienbox/alienbox';

// Box sizing partial styles:
// Apply a natural box layout model to all elements
*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

body {
	background-color: #2196F3;
	margin: 0;
	font-family: "Roboto", sans-serif;
}

ul {
	list-style-type: none;
	padding: 0px;
}

a {
	color: inherit;
	text-decoration: none;
}

body,
.pure-g [class *= "pure-u"],
pure-g-r [class *= "pure-u"] {
	font-family: "Roboto", sans-serif;
}

// Styling on warning for users on IE7 or below
.browsehappy {
  text-align: center;
  margin: 0;
  background: #fff8e2;
  color: #000;
  padding: 0.5em 0;
}
