// header styles
@import "../../../_styles/mixins/shadow";
.header {
  margin-bottom: 1em;
	padding: 1em;
	padding-left: 2em;
	padding-right: 2em;
  .head {
    h1,
    h2 {
      color: #FFFFFF;
      font-weight: 400;
      margin: 0;
    }
    h1 {
      font-size: 6em;
    }
    h2 {
      font-size: 3em;
			min-width: 350px;
    }
  }
	.social {
		color: #FFFFFF;
		margin: 0px;
		a {
			display: inline;
			margin: 10px;
			opacity: 0.9;
			transition: 0.3s;
		}
		a:hover {
			opacity: 1;
			transition: 0.3s;
		}
	}
}
