// alienbox styles
@import "../../../_styles/mixins/shadow";
.alienbox {
	background-color: #FFFFFF;
	margin: 0 auto;
	padding: 2em;
	padding-bottom: 4em;
	h1 {
		font-weight: 400;
		text-align: center;
		font-size: 4em;
		margin: 0;
		color: #FF6600;
	}
	.content {
		margin-top: 1em;
	}
	p {
		font-size: 1.2em;
		margin: .5em;
	}
	.screenshot {
		width: 90%;
	}
	.go {
		background-color: #FF6600;
		padding: 0.5em;
		text-align: center;
		color: #FFFFFF;
		font-size: 1.5em;
		border-radius: 5px;
		@include box-shadow(0px, 0px, 12px, 0px, rgba(255, 84, 0, 0.5));
		transition: 0.3s;
	}
	.go:hover {
		transition: 0.3s;
		background-color: #FF8330;
		@include box-shadow(0px, 0px, 12px, 2px, rgba(255, 84, 0, 0.5));
	}
}
